import { QrCodeWrapper } from "../style";
import { connect } from "react-redux";
import { actionCreators } from "../store";
const QrCode = (props) => {
  const {
    qrCodeUrl,
    initialQrCodeUrl,
    handleQrCodeMouseEnter,
    qrCodeMouseEnter,
  } = props;

  initialQrCodeUrl(qrCodeUrl);

  return (
    <div>
      <QrCodeWrapper
        qrcodeurl={qrCodeUrl}
        onMouseEnter={() => handleQrCodeMouseEnter(true)}
        onMouseLeave={() => handleQrCodeMouseEnter(false)}
      >
        <div className="left"></div>
        <div className="right">
          <div className="title">
            Download Jianshu App <span className="iconfont icon-jiantou"></span>
          </div>
          <div className="slogan">Find out more in App</div>
        </div>
        <div className={qrCodeMouseEnter ? "largeImg" : "largeImg hide"}>
          <div className="img"></div>
        </div>
      </QrCodeWrapper>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    qrCodeUrl: state.getIn(["home", "qrCodeUrl"]),
    qrCodeMouseEnter: state.getIn(["home", "qrCodeMouseEnter"]),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    initialQrCodeUrl(qrCodeUrl) {
      if (qrCodeUrl.length === 0) {
        dispatch(actionCreators.initialQrCodeUrlAction);
      }
    },

    handleQrCodeMouseEnter(value) {
      dispatch(actionCreators.qrCodeMouseEnterAction(value));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(QrCode);
