import { TopicItem, TopicWrapper } from "../style";
import { connect } from "react-redux";
import { actionCreators } from "../store";

const Topic = (props) => {
  const { topicList, initialTopicList } = props;

  initialTopicList(topicList);

  const generateTopicItem = () =>
    topicList.map((item) => {
      return (
        <TopicItem key={item.get("id")}>
          <img className="topicPic" alt="" src={item.get("imgUrl")} />
          {item.get("title")}
        </TopicItem>
      );
    });
  return <TopicWrapper>{generateTopicItem()}</TopicWrapper>;
};

const mapStateToProps = (state) => {
  return {
    topicList: state.getIn(["home", "topicList"]),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    initialTopicList(topicList) {
      if (topicList.size === 0) {
        dispatch(actionCreators.initialTopicListAction);
      }
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Topic);
