import { connect } from "react-redux";
import { actionCreators } from "../store";
import { ListWrapper, ListItem, LoadMore } from "../style";
import {Link} from 'react-router-dom'

const List = (props) => {
  const { initializeArticleList, articleList, loadArticleItem, loadMore } =
    props;

  initializeArticleList(articleList);
  const generateArticleListItem = () => {
    const loadList = articleList.slice(0, loadArticleItem);
    return loadList.map((item, index) => {
      return (
        <Link to={"/detail/" + item.get('id')} key={index}>
          <ListItem
            className={item.get("picUrl").length === 0 ? "no-pic" : "with-pic"}
            picUrl={item.get("picUrl")}
          >
            <div className="left">
              <div className="title">{item.get("title")}</div>
              <div className="abstract">{item.get("brief")}</div>
              <div className="meta">
                <span className="iconfont icon-diamond">
                  {item.get("diamondCount")}
                </span>
                <span className="iconfont"> {item.get("author")}</span>
                <span className="iconfont icon-dialogue">
                  {item.get("commentCount")}
                </span>
                <span className="iconfont icon-heart-fill">
                  {item.get("likeCount")}
                </span>
                <span
                  className={
                    item.get("thanksCount") === 0
                      ? "iconfont hide"
                      : "iconfont icon-shang "
                  }
                >
                  {item.get("thanksCount")}
                </span>
              </div>
            </div>
            <div className="pic"></div>
          </ListItem>
        </Link>
      );
    });
  };

  return (
    <ListWrapper>
      {generateArticleListItem()}
      <LoadMore
        className={articleList.size > loadArticleItem ? "" : "hide"}
        onClick={() => loadMore(loadArticleItem)}
      >
        Load more
      </LoadMore>
      <LoadMore
        onClick={() => loadMore(loadArticleItem)}
        className={articleList.size >= loadArticleItem ? "hide" : "bottom"}
      >
        Reach bottom
      </LoadMore>
    </ListWrapper>
  );
};

const mapStateToProps = (state) => ({
  articleList: state.getIn(["home", "articleList"]),
  loadArticleItem: state.getIn(["home", "loadArticleItem"]),
});

const mapDispatchToProps = (dispatch) => ({
  initializeArticleList(articleList) {
    if (articleList.size === 0) {
      dispatch(actionCreators.initializeArticleListAction);
    }
  },

  loadMore(value) {
    dispatch(actionCreators.loadMoreAction(value));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(List);
