import List from "./components/List";
import Recommend from "./components/Recommend";
import Topic from "./components/Topic";
import QrCode from "./components/QrCode";
import { HomeLeft, HomeRight, HomeWrapper } from "./style";
import bannerImage from '../../statics/banner.jpg'

const Home = () => {
  return (
    <HomeWrapper>       
      <HomeLeft>
           <img className='bannerImage' alt='' src={bannerImage}/>
        <Topic />
        <List />
      </HomeLeft>
      <HomeRight>
          <Recommend/>
          <QrCode/>
      </HomeRight>
    </HomeWrapper>
  );
};

export default Home;
