import { RecommendWrapper, RecommendItem } from "../style";
import { connect } from "react-redux";
import { actionCreators } from "../store";

const Recommend = (props) => {
  const { recommendPicLisst, initialRecommendList } = props;
  initialRecommendList(recommendPicLisst);
  const generateRecommendItem = () =>
    recommendPicLisst.map((item) => {
      return (
        <RecommendItem key={item.get("id")} backgroundUrl={item.get('imgUrl')} >
          {/* <img
            className="recommendPic"
            alt={item.get("imgUrl").split("/").at(-1)}
            src={item.get("imgUrl")}
          /> */}
        </RecommendItem>
      );
    });
  return <RecommendWrapper>{generateRecommendItem()}</RecommendWrapper>;
};

const mapStateToProps = (state) => {
  return {
    recommendPicLisst: state.getIn(["home", "recommendPicLisst"]),
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    initialRecommendList(recommendPicLisst) {
      if (recommendPicLisst.size === 0) {
        dispatch(actionCreators.initialRecommendListAction);
      }
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Recommend);
