import styled from "styled-components";

export const HomeWrapper = styled.div`
  width: 960px;
  margin: 0 auto;
`;

export const HomeLeft = styled.div`
  float: left;
  width: 625px;
  height: 200px;
  margin-top: 20px;
  .bannerImage {
    width: 625px;
    height: 270px;
  }
`;

export const HomeRight = styled.div`
  float: right;
  width: 280px;
  height: 200px;
`;

export const TopicWrapper = styled.div`
  overflow: hidden;
  padding: 20px 0 10px 0;
`;

export const TopicItem = styled.div`
  float: left;
  background: #f7f7f7;
  height: 32px;
  line-height: 32px;
  font-size: 14px;
  color: #000;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  padding-right: 10px;
  margin-left: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  .topicPic {
    display: block;
    float: left;
    margin-right: 10px;
    width: 32px;
    height: 32px;
  }
`;

export const RecommendWrapper = styled.div`
  height: 270px;
  margin-top: 20px;
`;

export const RecommendItem = styled.div`
  height: 54px;
  background: url(${(props) => props.backgroundUrl});
  cursor: pointer;
  background-size: contain;
  // .recommendPic {
  //   height: 54px;
  //   width: 280px;
  // }
`;

export const QrCodeWrapper = styled.div`
  position: relative;
  height: 88px;
  width: 100%;
  padding: 10px 22px;
  border: 1px solid #f0f0f0;
  border-radius: 6px;
  box-sizing: border-box;
  .left {
    float: left;
    margin-right: 10px;
    width: 60px;
    height: 60px;
    background: url(${(props) => props.qrcodeurl});
    background-size: contain;
    // background: red;
  }
  .right {
    margin-top: 11px;
    box-sizing: border-box;
    line-height: 20px;
    .title {
      font-size: 15px;
      color: #333333;
    }
    .slogan {
      font-size: 13px;
      color: #999999;
    }
  }
  .largeImg {
    position: absolute;
    display: flex;
    width: 100px;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    bottom: 100px;
    left: 70px;
    width: 150px;
    height: 150px;
    border: 1px solid #999999;
    border-radius: 6px;
    background: white;
    content-align: center;
    .img {
      background: url(${(props) => props.qrcodeurl});
      background-size: contain;
      width: 130px;
      height: 130px;
    }
    &.hide {
      display: none;
    }
  }
`;

// ListWrapper, ListItem, LoadMore;

export const ListWrapper = styled.div`
  width: 100%;
`;

export const ListItem = styled.div`
  width: 100%;
  // background:green;
  height: 124px;
  padding: 15px 2px 20px 0;
  margin: 0 0 15px;
  border-bottom: 1px solid #f0f0f0;
  .title {
    width: 100%;
    height: 27px;
    font-size: 17px;
    font-weight: 700;
    line-height: 1.5px;
  }
  .abstract {
    font-size: 13px;
    margin: 0 0 8px;
    line-height: 24px;
    color: #999;
  }
  .meta {
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    height: 20px;
    // background:red;
    .iconfont {
      font-size: 12px;
      margin: 0 10px 0 0;
      color: #b4b4b4;
      &.icon-diamond {
        color: #ea6f5a;
      }
      &.hide {
        display: none;
      }
    }
  }
  &.with-pic {
    .left {
      width: 458px;
      float: left;
    }
    .pic {
      float: right;
      width: 150px;
      height: 100px;
      border: 1px solid #f0f0f0;
      border-radius: 4px;
      background: url(${(props) => props.picUrl});
      background-size: contain;
    }
  }
`;

export const LoadMore = styled.button`
  backgroud: #333;
  width: 100%;
  border-radius: 20px;
  height: 40px;
  padding: 10px 15px;
  outline: 0;
  margin: 30px auto 60px;
  font-size: 15px;
  text-align: center;
  cursor: pointer;
  border: none;
  &.hide {
    display: none;
  }
  &.bottom {
    cursor: not-allowed;
  }
`;
